.header-color {
    display: none;
    background: #0F2027;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
    background: #0F2027;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    text-align: center;
}

.projects-grid h1 {
    font-size: 5vw;
    font-weight: bold;
    color: black;
    overflow-wrap: break-word;
    text-align: center;
}

.avatar-img {
    padding-top: 5em;
}

.avatar-img img {
    height: 250px;
    width: 250px;
    border-radius: 50%;
}

.banner-text {
    background-color: black;
    opacity: .7;
    width: 92%;
    margin: auto;
    border-radius: 10px;
}

.banner-text h1 {
    font-size: 6vw;
    font-weight: bold;
    color: white;
    overflow-wrap: break-word;
}

.banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}

.banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
}

.social-links {
    display: inline;
    justify-content: space-between;
    margin: auto;
}

.social-links i {
    color: white;
    font-size: 5em;
    padding: 8px;
}

@media screen and(max-width: 400 px) {
    .mdl-layout__header {
        display: none
    }
    .mdl-layout--fixed-header > .mdl-layout__header {
        display: -webkit-flex;display: -ms-flexbox;display: flex
    }
}